<template>
  <v-app dark>
    <v-content>
      <v-alert
        dismissible
        v-model="alert"
        type="error"
        transition="scale-transition"
        style="position:absolute;width:100%;margin:0px"
      >{{msg}}</v-alert>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex sm12 md4>
            <v-card light class="elevation-12">
              <v-toolbar dark color="red">
                <v-toolbar-title></v-toolbar-title>
                <v-spacer></v-spacer>
                <!--  -->
                <v-tooltip bottom>
                  <!--  -->
                  <!-- on  v-tooltip  -->
                  <template v-slot:activator="{on}">
                    <v-btn icon large v-on="on">
                      <v-icon large>info</v-icon>
                    </v-btn>
                  </template>
                  <!--  -->
                  <span>V3D</span>
                </v-tooltip>
              </v-toolbar>
              <v-card-text>
                <v-form>
                  <v-text-field
                    prepend-icon="phone"
                    name="phoneNumber"
                    label=""
                    type="tel"
                    color="red"
                    v-model="phoneNumber"
                    mask="###########"
                  />
                  <v-text-field
                    prepend-icon="lock"
                    name="password"
                    label=""
                    type="password"
                    color="red"
                    v-model="password"
                  />
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn dark color="red" @click="login"> </v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
export default {
  data: () => ({
    alert: false,
    msg: "",
    phoneNumber: "",
    password: ""
  }),
  methods: {
    login() {
      this.$http
        .post(
          "/user/login",
          {
            phoneNumber: this.phoneNumber,
            password: this.password
          },
          {}
        )
        .then(
          data => {
            console.info(data);
            if (data.body.code == 0) {
              this.$router.push("home");
            } else {
              this.alert = true;
              this.msg = data.body.msg;
            }
          },
          error => {
            console.info(error);
          }
        );
    }
  },
  beforeCreate() {
    this.$http.get("/user/hasLogin").then(
      data => {
        console.info(data);
        if (data.body.code == 0) {
          this.$router.push("home");
        }
      },
      error => {
        console.info(error);
      }
    );
  }
};
</script>

